<!-- 搜索页 -->
<template>
    <div class="search">
        <div class="top">
            <van-search v-model="value" placeholder="请输入搜索关键词" @search="onSearch" show-action>
                <template #action>
                    <div @click="onSearch" class="a">搜索</div>
                </template>
            </van-search>
        </div>
        <div class="hotsearch">
            <h5>热门搜索</h5>
            <button v-for="(i,index) in hSearch" :key="index" @click="aaa(i)">{{i}}</button>
        </div>
        <Recommen :item="bastList" v-if="list.length ==''&value!=''"></Recommen>
        <div class="resultlist">
            <FirstList :item="i" v-for="i in list" :key="i.id"></FirstList>
        </div>
    </div>
</template>

<script>
    // @ is an alias to /src
    import Recommen from '@/components/Recommen'
    import FirstList from '@/components/FirstList'

    export default {
        name: 'Search',
        data() {
            return {
                value: '',
                bastList: [],
                firstList: [],
                info: [],
                list: [],
                hSearch: ['吸尘器', '鞋', '女包', '洗衣机']
            }
        },
        created() {
            this.$axios.get('api/index').then(data => {
                this.bastList = data.data.data.info.bastList;
            })

        },
        components: {
            Recommen, FirstList
        },
        methods: {
            onSearch() {
                // console.log(this.value);
                this.$axios.get('http://47.115.51.185/api/products?keyword=' + this.value).then(data => {
                    // console.log(data);
                    this.list = data.data.data;
                })
            },
            aaa(v) {

                this.value = v;
                this.onSearch();
            }
        }
    }
</script>

<style lang="less" scoped>
    .search {
        .resultlist {
            background-color: #fff;

            .firstList {
                margin-top: 0px;
                margin-bottom: 0px;
                padding-bottom: 5vw;


            }

        }

        .top {
            width: 100%;
            display: flex;
        }



        .van-cell {
            width: 75vw;
            margin-left: 3vw;

        }

        .van-search__content {
            border-radius: 20px;
        }

        .hotsearch {
            padding-left: 4vw;
            margin-top: 10px;
            margin-bottom: 10px;
            border-bottom: 2px solid #F5F5F5;

            h5 {
                color: #C0C0C0;
            }

            button {
                margin-top: 15px;
                margin-bottom: 15px;
                background-color: #fff;
                border: 1px solid #AFAFAF;
                color: #454545;
                margin-right: 10px;
                font-size: 0.8rem;
                padding: 5px 10px;
            }
        }
    }
</style>